import styled from 'styled-components';
import { SNSList } from './ForList';
import { SNSs } from '../WorksAndEducations';

export const Introduction = () => {
    return (
        <>
            <Sul>
                <Sli>
                    <SName> </SName>
                </Sli>
                <Sli>
                    <SNSList sns={SNSs}/>
                </Sli>
            </Sul>
            <IntroSentence />
        </>
    )
}


const SName = styled.div`
    font-size: 30px;
    font-weight: bold;
    color: #000000; 
`

const Sli = styled.li`
    list-style-type: none;
    display: inline-flex;
    margin: 0 5px;
`

const Sul = styled.ul`
    display: flex;
`

const IntroSentence = () => {
    return (
        <p>{text}</p>
    )
}

const text = `
2020451
20224

20253(NAIST)

`